.breed-select {
  min-width: 255px;
  margin-bottom: 20px;
  font-size: 40px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #ff3d00;
  border-color: #ff3d00 rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotate 1s linear infinite;
  display: none;
}

.loader:before, .loader:after {
  content: "";
  box-sizing: border-box;
  border: 5px solid #fff;
  border-radius: 50%;
  animation: prixClipFix 2s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.loader:after {
  border-color: #440e8a;
  animation: prixClipFix 2s linear infinite, rotate .5s linear infinite reverse;
  top: 6px;
  bottom: 6px;
  left: 6px;
  right: 6px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

